.about-container {
  margin-top: 62px;
  .content-size {
    img {
      max-width: 900px;
    }
  }
}
@media (min-width: 320px) and (max-width: 480px) {
  .about-container {
    text-align: center;
    .content-size {
      max-width: 380px;
      img {
        max-width: 380px;
        width: 100%;
      }
    }
  }
}
